/*=============== Header ===============*/
.navigation-fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 19998;
}
.navigation-hidden .nav-header {
  display: none;
}
header#header {
  min-height: 80px;
}
.nav-menus-wrapper {
  width: 100%;
}
ul.nav-menu.float-right {
  top: 0px;
  right: 0px;
  position: absolute;
}
ul.nav-menu.float-right {
  margin-right: 50px;
}
.main_header_area {
  position: fixed;
  top: 0px;
  padding-top: 8px;
  height: 80px;
  width: 100%;
  z-index: 999;
  color: #fff;
}
.main_header_area.scrolled {
  background: #fff;
  color: #fff;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
}
.nav-brand {
  line-height: 70px;
  padding: 0;
  color: #343a40;
  font-size: 24px;
  text-decoration: none !important;
}
.nav-brand:hover,
.nav-brand:focus {
  color: #343a40;
}
.navigation-portrait .nav-brand {
  font-size: 18px;
  line-height: 48px;
}
.nav-logo > img {
  height: 48px;
  margin: 11px auto;
  padding: 0 15px;
  float: left;
}
.nav-logo:focus > img {
  outline: medium none invert;
  outline: initial;
}
.navigation-portrait .nav-logo > img {
  height: 36px;
  margin: 6px auto 6px 15px;
  padding: 0;
}
.nav-toggle {
  width: 30px;
  height: 30px;
  padding: 6px 2px 0;
  position: absolute;
  top: 50%;
  margin-top: -14px;
  left: 40px;
  display: none;
  cursor: pointer;
}
.nav-toggle > span:nth-child(1) {
  width: 26px;
}
.nav-toggle > span {
  display: block;
  background-color: rgb(13, 17, 54);
  border-radius: 6px;
  height: 3px;
  margin-bottom: 5px;
  transition: all 0.2s ease-in-out 0s;
}
.nav-toggle > span:nth-child(2) {
  width: 12px;
}
.nav-toggle > span:last-child {
  margin-bottom: 0px;
}
.nav-toggle > span:nth-child(3) {
  width: 22px;
}
.navigation-portrait .nav-toggle {
  display: block;
}
.navigation-portrait .nav-menus-wrapper {
  width: 316px;
  height: 100%;
  top: 0;
  left: -400px;
  position: fixed;
  background-color: #fff;
  z-index: 20000;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transition-duration: 0.2s;
  transition-timing-function: ease;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.15);
}
.navigation-portrait .nav-menus-wrapper.nav-menus-wrapper-right {
  left: auto;
  right: -400px;
}
.navigation-portrait .nav-menus-wrapper.nav-menus-wrapper-open {
  left: 0;
}
.navigation-portrait
  .nav-menus-wrapper.nav-menus-wrapper-right.nav-menus-wrapper-open {
  left: auto;
  right: 0;
}
.nav-menus-wrapper-close-button {
  width: 30px;
  height: 40px;
  margin: 10px 7px;
  /* display: none; */
  margin-left: 30px;
  margin-top: 15px;
  color: #343a40;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}
.navigation-portrait .nav-menus-wrapper-close-button {
  display: block;
}

.navigation-landscape .nav-menu.nav-menu-centered > li {
  float: none;
}
.nav-menu > li {
  display: inline-block;
  text-align: left;
}
.navigation-portrait .nav-menu > li {
  width: 100%;
  position: relative;
}
.nav-menu + .nav-menu > li:first-child {
  border-top: none;
}
.nav-menu > li > a {
  height: 70px;
  padding: 26px 15px;
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  color: rgb(13, 17, 54);
  transition: all 0.15s ease-in-out 0s;
}
.nav-menu > li > a:hover {
  color: rgb(0, 158, 127);
}
.navigation-portrait .nav-menu > li > a {
  width: 100%;
  height: auto;
  padding: 12px 15px 12px 26px;
}
.nav-menu > li > a > i,
.nav-menu > li > a > [class*="ion-"] {
  width: 18px;
  height: 16px;
  line-height: 16px;
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
}
.nav-menu > li > a > [class*="ion-"] {
  width: 16px;
  display: inline-block;
  -webkit-transform: scale(1.8);
          transform: scale(1.8);
}
.navigation-portrait .nav-menu.nav-menu-social {
  width: 100%;
  text-align: center;
}
.nav-menu.nav-menu-social > li {
  text-align: center;
  float: none;
  border: none !important;
}
.navigation-portrait .nav-menu.nav-menu-social > li {
  width: auto;
}
.nav-menu.nav-menu-social > li > a > [class*="ion-"] {
  font-size: 12px;
}
.nav-menu.nav-menu-social > li > a > .fa {
  font-size: 14px;
}
.navigation-portrait .nav-menu.nav-menu-social > li > a {
